}

.assignearning-inner h5 {
  font-size: 18px;
  max-width: 200px;
  padding: 5px 15px;
}

.about-calender {
  background-color: #4e2fa9;
  border-radius: 50px;
  margin-right: 15px;
  height: 40px;
  width: 40px;
