}

.user-trust {
  background-color: #fff;
  border-radius: 20px;
  text-align: center;
  max-width: 220px;
  box-shadow: 5px 4px 7px 0px #c8c8c8;
}

.user-count {
  font-size: 4rem;
  color: #ff4e00;
}

